* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.timer {
  display: flex;
}

.field {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin-right: 12px;
}

.value {
  font-size: 36px;
}

.label {
  text-transform: uppercase;
}
.buttons {
  display: flex;

  justify-content: center;
  padding-top: 200px;
}
.one-button {
  font-size: 20px;
  font-weight: 700;
  line-height: 1.8;

  border-color: antiquewhite;
  width: 100px;
  height: 50px;

  margin-right: 50px;
}

.two-button {
  font-size: 20px;
  font-weight: 700;
  line-height: 1.8;


  border-color: antiquewhite;
  width: 100px;
  height: 50px;
}